<template>
  <div>
    <publicgray-title></publicgray-title>
    <map-nav></map-nav>
    <div class="content">
      <div class="steps">
        <!-- <el-timeline :reverse="reverse">
          <el-timeline-item
            v-for="(activity, index) in activities"
            :key="index"
            :timestamp="activity.timestamp">
            {{activity.content}}
          </el-timeline-item>
        </el-timeline> -->
      </div>
    </div>

  </div>

</template>

<script>
import Vue from 'vue';
import VueAMap from 'vue-amap';
Vue.use(VueAMap);
// vue-amap
VueAMap.initAMapApiLoader({
  // key
  key: '04e2d9e82b3450b6a3eeb93caffbd890',
  // 
  plugin: ['AMap.Autocomplete', 'AMap.PlaceSearch', 'AMap.Scale', 'AMap.OverView', 'AMap.ToolBar', 'AMap.MapType', 'AMap.PolyEditor', 'AMap.CircleEditor'],
  //  sdk  1.4.4
  v: '1.4.4',
  uiVersion: '1.0.11' // 
});
    import PublicgrayTitle from "../../components/publicgrayTitle";
    import MapNav from "../../components/mapNav";
    export default {
        name: "logistics",
      components: {MapNav, PublicgrayTitle},
      data(){
          return{
            reverse: false,
            activities: [
            {
              content: '',
              timestamp: ''
            }, {
              content: '',
              timestamp: '2018-04-13 15:45'
            }, {
              content: '',
              timestamp: '2018-04-13 1330'
            },{
              content:'',
              timestamp: '2018-04-13 1328'
            },{
              content:'',
              timestamp: '2018-04-13 1300'
            },{
              content:'',
              timestamp: '2018-04-13 1228'
            },{
              content:'',
              timestamp: '2018-04-13 1000'
            }],
            value4: null
          }
      }
    }
</script>

<style lang="scss" scoped>
.content{
  width: 100%;
  .steps{
    background: white;width: 100%;
    padding: 20px;
    box-sizing: border-box;
  }
}
</style>



